.App {
  max-width: 1024px;
  width: 100%;
  min-height: 500px;
  border: 1px solid #f9c0f9;
  border-radius: 5px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, .15);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 1em .8em;

  display: grid;
  grid-template-rows: .4fr 2fr;
  align-items: center;
}

.AppTitle {
  text-align: center;
  font-size: 2em;
  font-family: Calibre;
  font-weight: 500;
}
